/* main element */
.weather {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
}
/* pseudo elements: positioning and setup */
.weather:before,
.weather:after {
  content: "";
  position: absolute;
  left: -20%;
  top: -20%;
  right: -20%;
  bottom: -20%;
  z-index: 2;
  pointer-events: none;

  background: transparent repeat;
  background-size: 150px 800px;
  -ms-interpolation-mode: nearest-neighbor;
  image-rendering: -webkit-crisp-edges;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: pixelated;
  opacity: 0.7;
  transform: rotate(10deg);

  -webkit-animation-name: weather;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-name: weather;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.weather:after {
  opacity: 0.8;
  -webkit-animation-name: weather2;
  animation-name: weather2;
}
/* rain (fast, rain.png) */
.weather.rain:before,
.weather.rain:after {
  background-image: url(data:image/png;base64,);
  -webkit-animation-duration: 1.25s;
  animation-duration: 1.25s;
}
.weather.rain:after {
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
}
/* snow (slow, snow.png) */
.weather.snow:before,
.weather.snow:after {
  background-image: url(data:image/png;base64,);
  -webkit-animation-duration: 6s;
  animation-duration: 6s;
}
.weather.snow:after {
  -webkit-animation-duration: 4.5s;
  animation-duration: 4.5s;
}
/* animation keyframes */
@-webkit-keyframes weather {
  from {
    background-position: 0 0px;
  }
  to {
    background-position: 0 1024px;
  }
}
@keyframes weather {
  from {
    background-position: 0 0px;
  }
  to {
    background-position: 0 1024px;
  }
}
@-webkit-keyframes weather2 {
  from {
    background-position: 64px 64px;
  }
  to {
    background-position: 64px 1088px;
  }
}
@keyframes weather2 {
  from {
    background-position: 64px 64px;
  }
  to {
    background-position: 64px 1088px;
  }
}

.rsizer {
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at bottom, #0b011d 0%, #06000f 100%);
  overflow: hidden;
}


.lamp {
  position: absolute;
  height: 120px;
  width: 120px;
  top: 0px;
  left: 22px;
  background: url(/ambient-sound-mixer/static/media/lamp.783db8ab.png) no-repeat;
  background-size: cover;
  background-position-y: center;
  background-position-x: center;
  transform-origin: top;
  -webkit-animation: lampsway 1s infinite;
          animation: lampsway 1s infinite;
}

.glowin {
  position: absolute;
  height: 60px;
  width: 60px;
  top: 45px;
  left: 40px;
  border-radius: 50%;
  transform-origin: top;
  opacity: 0.3;
  -webkit-animation: lampglow 1.3s infinite;
          animation: lampglow 1.3s infinite;
  border: solid 1px red;
}

@-webkit-keyframes lampsway {
  0% {
    transform: rotate(9deg);
  }

  50% {
    transform: rotate(8deg);
  }

  100% {
    transform: rotate(9deg);
  }
}

@keyframes lampsway {
  0% {
    transform: rotate(9deg);
  }

  50% {
    transform: rotate(8deg);
  }

  100% {
    transform: rotate(9deg);
  }
}

