  height: 160px;
  width: 150px;
  top: -10px;
  background-position-y: center;
  background-position-x: center;
  background-size: cover;
  -webkit-animation: lightningshape 7.5s steps(1, end) infinite;
          animation: lightningshape 7.5s steps(1, end) infinite;
